import { AbstractControl } from '@angular/forms';
export class PasswordValidation {

    static MatchPassword(AC: AbstractControl) {
       const password = AC.get('password').value; // to get value in input tag
       const confirmPassword = AC.get('confirmPassword').value; // to get value in input tag
        if (password !== confirmPassword) {
            AC.get('confirmPassword').setErrors( {MatchPassword: true} );
        } else {
            return null;
        }
    }

    static DateValidation(AC: AbstractControl) {
        const fromdate = AC.get('fromdate').value; // to get value in input tag
        const todate = AC.get('todate').value; // to get value in input tag
        if (fromdate > todate) {
            AC.get('todate').setErrors({ DateValidation: true });
        } else {
            return null;
        }
    }
}



